<template>
  <div class="shop">
      <div :data-num="car.mynum" @click="getNum($event,index,)" class="one" v-for="(car,index) in cars" :key="car.index">
            
            <router-link :to="{path: '/' + car.url}">
                      动态绑定路由
            </router-link>
      </div>
     

  </div>

</template>
<script>
import { constants } from 'fs';
  export default {
    data() {
      return {
          cars:[
            {
              mynum:"111",
              id:"kkkkk",
              url:'home/one'
            },
            {
              mysnum:"222",
               url:"www.kkkk.com"
            }
          ]
      }
    },
    methods:{
      getNum(e,index){
          console.log(e.target.dataset.num)
          // 取自定义num
          console.log(this.cars[index].id)
          // 取文章id
          console.log(index+1)

          // 取索引
      }
    }
  }

</script>
<style>
  .test {
    width: 50px;
    height: 50px;
    background-color: red;
  }
  .one{
    height: 20px;
    width: 20px;
 
   
    
  }
  
</style>



